.ns-app{position:relative;margin:30px;width:500px;min-height:300px;background-color:#eee;padding:20px;border-radius:5px;overflow:hidden}.ns-app__user-list__item{display:flex;padding:10px;border-radius:5px;background-color:#ddd}.ns-app__user-list__item:nth-child(2n){background-color:#fff}.ns-app__user-list__item:not(:last-child){margin-bottom:10px}.ns-app__user-list__item__avatar{width:30px;vertical-align:middle;margin-right:10px;border-radius:3px}.ns-app__user-list__item__details__name{font-size:1.4rem;line-height:1.5;font-weight:600}.ns-app__user-list__item__details__email{font-size:1rem;opacity:.85}